<template>
  <div class="user-buyed">
    <div @touchmove.prevent class="chain-tab">
      <div class="chain-tab-ul">
        <div class="chain-tab-li">
          <router-link :to="'userMealList'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">套餐商品</span>
            </div>
          </router-link>
        </div>
        <div class="chain-tab-li">
          <router-link :to="'integralList'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">积分商品</span>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <keep-alive>
        <router-view class="chain-tab-view" @touchstart.native="scrollFixStart" @touchmove.native="scrollFixMove"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "user-buyed",
  data() {
    return {};
  },
  components: {},
  activated() {},
  created() {
    console.log("代理tab执行created方法");
  },
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {
    scrollFixStart(e){
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e){
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.user-buyed {
    width: 100%;
    padding-top: 0.66rem;
    box-sizing: border-box;
    flex: 1;
    display flex;
    flex-direction column;

    .chain-tab-view {
        box-sizing: content-box;
        padding-top: 0.1rem;
        flex: 1;
        overflow: auto;
        overflow-x: hidden;
    }

    .chain-tab {
        position: fixed;
        top: 0.3rem;
        height: 0.36rem;
        background-color: #fff;
        width: 100%;
        z-index 10;

        .chain-tab-ul {
            margin 0px auto;
            height: 100%;
            width: 80%;
            display: flex;
            align-items: center;
            text-align: center;

            .chain-tab-li {
                overflow: hidden;
                height: 100%;
                flex: 1;

                .chain-tab-li-hot {
                    width: 100%;
                    height: 100%;
                    position: relative;

                    .chain-name {
                        line-height: 0.36rem;
                        color: #42495C;
                        font-size: 0.12rem;
                        position relative;
                        top 0.01rem;
                        letter-spacing 0.01rem;
                    }
                }

                .chain-tab-li-hot::after {
                    position: absolute;
                    bottom: -3px;
                    left: 15%;
                    width: 70%;
                    height: 2px;
                    background: #FD8709;
                    border-radius 1px;
                    content: '';
                    -webkit-transition: -webkit-transform 0.2s;
                    transition: transform 0.2s;
                    -webkit-transform: translate3d(0, 0, 0);
                    transform: translate3d(0, 0, 0);
                }

                .active {
                    .chain-name {
                        top 0px;
                        color: #FD8709;
                    }

                    .chain-tab-li-hot::after {
                        -webkit-transform: translate3d(0, -150%, 0);
                        transform: translate3d(0, -150%, 0);
                    }
                }
            }
        }
    }
}
</style>



